export default {
	data() {
		return {
			user:'',
			password:'',
		}
	},
	methods: {
		//跳转到注册页面
		toRegister:function(){
			uni.navigateTo({
			    url: '../register/register',
			});
		},
		// 获取用户名/邮箱
		getUserName:function(e){
			// console.log(e);
			this.user = e.detail.value;
			// console.log(this.user);
		},
		// 获取密码
		getUserPassword:function(e){
			// console.log(e);
			this.password = e.detail.value;
			// console.log(this.password);
		},
		// 登录提交
		login:function(){
			uni.request({
				url:'tp5.1.com/index/index',
				data:{
					email:this.email,
				},
				method:'POST',
				success: (data) => {
					console.log(data);
				}
			})
			// if(this.user && this.password){
			// 	console.log("提交成功");
			// }
		}
	}
}
